<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <section class="card-group row">
    <div class="card">
      <div class="card-body d-flex align-items-start text-uppercase">
        <span class="h4 me-3">step 1</span><span class="h1 titleText">your details</span>
      </div>
    </div>
  </section>
  <section class="card-group row">
    <div class="card col-7">
      <div class="card-body">
        <VForm v-slot="{ errors }">
          <table class="table align-middle table-light table-striped-columns">
            <tbody>
              <!-- id -->
              <tr>
                <th scope="row" class="">會員帳號<br />ID</th>
                <td>{{ profileData.email }}</td>
              </tr>
              <!-- name -->
              <tr>
                <th scope="row">姓名<br />Name *</th>
                <td>
                  <VField name="姓名" type="text" rules="required" v-model="profileData.name" />
                  <ErrorMessage name="姓名" class="text-danger ms-3" />
                </td>
              </tr>
              <!-- phone -->
              <tr>
                <th scope="row">行動電話<br />Mobile *</th>
                <td>
                  <VField
                    name="電話"
                    type="text"
                    :class="{ 'is-invalid': errors['電話'] }"
                    :rules="isPhone"
                    v-model="profileData.phone"
                  />
                  <ErrorMessage name="電話" class="text-danger ms-3" />
                </td>
              </tr>
              <!-- coupon -->
              <tr>
                <th scope="row" class="text-nowrap">折價卷<br />Coupon</th>
                <td>
                  <select class="form-select form-select" aria-label="Small select example">
                    <option selected>Open this select coupon</option>
                    <option value="1">Lulu so cute -100</option>
                    <option value="2">Line Friend -50</option>
                    <option value="3">Birth Day gift -100</option>
                  </select>
                </td>
              </tr>
              <!-- pickup -->
              <tr>
                <th scope="row">取貨方式<br />Pickup *</th>
                <td>
                  <template v-for="(item, index) in pickup" :key="index">
                    <a
                      href="#"
                      class="ms-5 text-decoration-none position-relative"
                      @click.prevent="user.pickup = item"
                      >{{ item }}<CircleOne :class="{ 'd-none': user.pickup !== item }"
                    /></a>
                  </template>
                </td>
              </tr>
              <!-- address -->
              <template v-if="user.pickup === 'Home delivery'">
                <tr>
                  <th scope="row" class="text-nowrap">Date<br />Pickup Date *</th>
                  <td>
                    <template v-for="(item, index) in pickupDate" :key="index">
                      <a
                        href="#"
                        class="ms-5 text-decoration-none position-relative"
                        @click.prevent="user.pickupDate = item"
                        >{{ item }}<CircleTwo :class="{ 'd-none': user.pickupDate !== item }"
                      /></a>
                    </template>
                  </td>
                </tr>
              </template>
              <!-- pickup Date && Time -->
              <template v-else>
                <tr>
                  <th scope="row" class="text-nowrap">Date<br />Pickup Date *</th>
                  <td>
                    <template v-for="(item, index) in pickupDate" :key="index">
                      <a
                        href="#"
                        class="ms-5 text-decoration-none position-relative"
                        @click.prevent="user.pickupDate = item"
                        >{{ item }}<CircleTwo :class="{ 'd-none': user.pickupDate !== item }"
                      /></a>
                    </template>
                  </td>
                </tr>
                <!-- pickup Time -->
                <tr>
                  <th scope="row" class="text-nowrap">Time<br />Pickup Time *</th>
                  <td>
                    <template v-for="(item, index) in pickupTime" :key="index">
                      <a
                        href="#"
                        class="ms-5 text-decoration-none position-relative"
                        @click.prevent="user.pickupTime = item"
                        >{{ item }}<CircleThree :class="{ 'd-none': user.pickupTime !== item }"
                      /></a>
                    </template>
                  </td>
                </tr>
              </template>
              <!-- address -->
              <tr :class="{'d-none': user.pickup !== 'Home delivery'}">
                <th scope="row" class="text-nowrap">收件地址<br />Location *</th>
                <td>
                  <Address :addressData="profileData.address" />
                </td>
              </tr>
            </tbody>
          </table>
        </VForm>
      </div>
    </div>
    <div class="card col-5">
      <div class="card-body">
        <Note />
      </div>
    </div>
  </section>
</template>

<style lang="scss" scoped>
.circle-1 {
  width: 8rem;
}
.circle-2 {
  width: 8rem;
}
.circle-3 {
  width: 8rem;
}
</style>

<script>
import Note from '@/components/models/NoteModel.vue';
import Address from '@/components/models/AddressModal.vue';
import CircleOne from '../models/circle/CircleOne.vue';
import CircleTwo from '../models/circle/CircleTwo.vue';
import CircleThree from '../models/circle/CircleThree.vue';

export default {
  data() {
    return {
      pickup: ['Home delivery', 'Lulu shop'],
      pickupDate: ['12/9', '12/10', '12/11', '12/12', '12/13'],
      pickupTime: ['10AM-2Pm', '2PM-6Pm', '6PM-10Pm'],
      user: {
        pickup: 'Home delivery',
        pickupDate: '12/9',
        pickupTime: '10AM-2Pm',
      },
      profileData: {},
    };
  },
  props: ['carts', 'profile'],
  watch: {
    profile() {
      this.profileData = JSON.parse(JSON.stringify(this.profile));
    },
  },
  components: {
    CircleOne,
    CircleTwo,
    CircleThree,
    Note,
    Address,
  },
  methods: {
    isPhone(value) {
      const phoneNumber = /^(09)[0-9]{8}$/;
      return phoneNumber.test(value) ? true : '需要正確的電話號碼';
    },
  },
};
</script>
